.elements {
  margin-top: 51px;
  display: grid;
  grid-gap: 20px 17px;
  grid-template-columns: repeat(auto-fill, minmax(282px, 282px));
  grid-template-rows: repeat(2, 361px);
  justify-content: center;
}

@media screen and (max-width: 703px) {
  .elements {
    margin: 36px 19px 0;
  }
}